/* eslint-disable operator-linebreak */
import moment from 'moment';
import {
  // Avatar,
  Box,
  Button,
  Card,
  CardActions,
  CardContent,
  Divider,
  Typography,
  Container,
  Grid
} from '@material-ui/core';
import { useNavigate, useParams } from 'react-router';
import { useEffect, useState } from 'react';
import { makeGetRequest } from 'src/services/httpservice';

const BusinessDetails = (props) => {
  const { id } = useParams();
  const navigate = useNavigate();
  const [details, setDetails] = useState(null);

  function getDetails(serviceProviderId) {
    makeGetRequest(`businesses/${serviceProviderId}`)
      .then((response) => response.json())
      .then((responseJson) => {
        if (responseJson.status) {
          setDetails(responseJson.data.business);
        } else {
          console.log('bad status returned for business');
        }
      })
      .catch((error) => {
        console.error(error);
      });
  }

  useEffect(() => {
    getDetails(id);
  }, [id]);

  return (
    <>
      <Card {...props}>
        <CardContent>
          <Box
            sx={{
              // alignItems: 'center',
              display: 'flex',
              flexDirection: 'column'
            }}
          >
            <Typography color="textPrimary" gutterBottom variant="h3">
              Business Details
            </Typography>
          </Box>
        </CardContent>
      </Card>

      {details && (
        <Card>
          <CardContent>
            <Box
              sx={{
                display: 'flex',
                flexDirection: 'column'
              }}
            >
              <Container maxWidth={false}>
                <Grid container spacing={3}>
                  <Grid item xs={4}>
                    <Typography color="textSecondary" variant="body1">
                      Business Name
                    </Typography>
                    <Typography color="textPrimary" gutterBottom variant="h4">
                      {details.name}
                    </Typography>
                  </Grid>

                  <Grid item xs={4}>
                    <Typography color="textSecondary" variant="body1">
                      Business Type
                    </Typography>
                    <Typography color="textPrimary" gutterBottom variant="h5">
                      {details.type}
                    </Typography>
                  </Grid>

                  <Grid item xs={4}>
                    <Typography color="textSecondary" variant="body1">
                      Phone Number
                    </Typography>
                    <Typography color="textPrimary" gutterBottom variant="h5">
                      {details.phone}
                    </Typography>
                  </Grid>
                </Grid>

                <Grid container paddingTop={4} spacing={3}>
                  <Grid item xs={4}>
                    <Typography color="textSecondary" variant="body1">
                      Address
                    </Typography>
                    <Typography color="textPrimary" gutterBottom variant="h5">
                      {details.address}
                    </Typography>
                  </Grid>

                  <Grid item xs={4}>
                    <Typography color="textSecondary" variant="body1">
                      Bio
                    </Typography>
                    <Typography color="textPrimary" gutterBottom variant="h5">
                      {details.bio}
                    </Typography>
                  </Grid>

                  <Grid item xs={4}>
                    <Typography color="textSecondary" variant="body1">
                      Created At
                    </Typography>
                    <Typography color="textPrimary" gutterBottom variant="h5">
                      {`${moment().format('hh:mm A')} ${details.created_at}`}
                    </Typography>
                  </Grid>
                </Grid>
              </Container>
            </Box>
          </CardContent>
          <Divider />
          <CardActions>
            <Button
              onClick={() => {
                navigate(`/app/businesses/${id.toString()}/edit`);
              }}
              color="primary"
              fullWidth
              variant="text"
            >
              Edit Business Details
            </Button>
          </CardActions>
        </Card>
      )}
    </>
  );
};

export default BusinessDetails;
